.header-nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 4;

  display: none;

  @media (min-width: $tablet-width) {
    position: relative;

    display: flex;

    @media (min-width: $desktop-width) {
      background-color: transparent;
    }
  }
}

.header-nav__toggle {
  @media (min-width: $tablet-width) {
    display: none;
  }
}

.header-nav__toggle:active + .header-nav__list {
  display: block;
}
